.result-button {
    width: 200px;
    height: 50px;
    background-image: linear-gradient(to right, rgb(255, 98, 150) , rgb(220, 42, 72));
    padding: 10px;
    color: white;
    font-size: 15px;
    font-family: 'Bree Serif';
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    border-style: none;
    outline: none;
  }
  .result-button:hover{
    background-image: linear-gradient(to right, rgb(152, 187, 247),rgb(0, 59, 222));
    color: white;
  }
  