
.Rps {
    margin-top: 20px;
    text-align : center;
    font-size: 40pt;
    letter-spacing: 5pt;
  
  }
  
  .Rps img.start {
    max-width: 300px;
    cursor: pointer;
    margin: auto;
  }
  
  .Rps .Game {
    display: flex;
    letter-spacing: 2pt;
  }
  .Rps .Game .Player {
    flex-grow: 1;
  }
  .Rps .Game .Computer {
    flex-grow: 1;
  }
  
  .Rps .Game .Player.selected img,
  .Rps .Game img {
    max-width: 150px;
    margin: auto;
  }
  .Rps .Game .Player.selected img {
    transform: rotateY(180deg);
  }
  
  .Rps .Game .Player .choose img {
    max-width: 85px;
    transform: rotateY(180deg);
    display: block;
  
  }
  
  .Rps .Game .Player span {
    cursor: pointer;
    display: inline-block;
    text-align: center;
    margin: 0 15px;
    font-size: 12pt;
    letter-spacing: 3pt;
  }
  
   
  
  .Rps pre {
    padding: 10px;
    text-align: left;
    font-size: 10pt;
    letter-spacing: normal;
  }
  
  .Rps .Results {
    letter-spacing: normal;
    font-size: 50pt;
  }
  
  .Rps .Results img {
    display:block;
    max-width: 100px;
    cursor: pointer;
    margin:auto;
  }
  
  .Rps input {
    font-size: 0.75em;
    text-align: center;
    width: 90%;
    margin: 0 auto 25px;
    padding: 10px;
    line-height: 1;
    background-color: #eef;
    border: 2px solid #ccf;
    border-radius: 5px;
    display: block;
  }