.trigger-button {
    width: 200px;
    height: 50px;
    background-color: #9b5634;
    padding: 20px;
    color: white;
    font-family: 'Bree Serif';
    font-size: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    border-style: none;
    outline: none;
    cursor: pointer;
  }
  .trigger-button:hover {
    background-color: #c98860;
    color: black;
  }
  
  .trigger-button-close {
    background-color: transparent;
    width: 20px;
    height: 20px;
    font-size: 18px;
    border-style: none;
    outline: none;
    align-self: flex-end;
    margin-bottom: 5px;
  }
  