
  .rps {
    font-family: 'Press Start 2P', cursive;
    font-family: 'Square Peg', cursive;
    font-family: 'Syne Mono', monospace;
    text-align : center;
    font-size: 40pt;
    letter-spacing: 5pt;
  }
  
  .rps img.start {
    max-width: 250px;
    cursor: center;
    margin:auto;
  }
  
  .rps .Game {
    display: flex;
    letter-spacing: 2pt;
  }
  .rps .Game .Player {
    flex-grow: 1;
  }
  .rps .Game .Computer {
    flex-grow: 1;
  }
  
  .rps .Game .Player.selected img,
  .rps .Game img {
    max-width: 150px;
    margin: auto;
  }
  .rps .Game .Player.selected img {
    transform: rotateY(180deg);
  }
  
  .rps .Game .Player .choose img {
    max-width: 85px;
    transform: rotateY(180deg);
    display: block;
  
  }
  
  .rps .Game .Player span {
    cursor: pointer;
    display: inline-block;
    text-align: center;
    margin: 0 15px;
    font-size: 12pt;
    letter-spacing: 3pt;
  }
  
   
  
  .rps pre {
    padding: 10px;
    text-align: left;
    font-size: 10pt;
    letter-spacing: normal;
  }
  
  .rps .Results {
    letter-spacing: normal;
    font-size: 50pt;
  }
  
  .rps .Results img {
    display:block;
    max-width: 100px;
    cursor: pointer;
    margin:auto;
  }
  
  .rps input {
    font-family: inherit;
    font-size: 0.75em;
    text-align: center;
    width: 90%;
    margin: 0 auto 25px;
    padding: 10px;
    line-height: 1;
    background-color: #143d59;
    border: 2px solid #143d59;
    border-radius: 5px;
    display: block;
  }
  
  