body {
   background-color: #cfdfff;
 }
 .Appx {
   font-family: 'Roboto', sans-serif;
   font-size: 35pt;
   text-align: center;
   letter-spacing: 5pt;
 }
 .Appx img.start {
   max-width: 250px;
   cursor: pointer;
 }
 .Appx .Game {
   display: flex;
   letter-spacing: 2pt;
 }
 .Appx .Game .Player {
   flex-grow: 1;
 }
 .Appx .Game .Computer {
   flex-grow: 1;
 }
 .Appx .Game .Player span {
   cursor: pointer;
   display: inline-block;
   text-align: center;
   font-size: 0.75em;
 }
 .Appx .Game .Player img {
   max-width: 200px;
   margin: 0 15px 15px;
   display: block;
 }
 .Appx .Game .Player.selected img,
 .Appx .Game img {
   max-width: 250px;
   margin: auto;
 }
 .Appx pre {
   padding: 10px;
   text-align: left;
   font-size: 20pt;
   letter-spacing: normal;
 }
 .Appx .Results {
   letter-spacing: normal;
   font-size: 50pt;
 }
 .Appx .Results img {
   display: block;
   max-width: 100px;
   cursor: pointer;
   margin: auto;
 }
 .Appx input {
   font-family: inherit;
   font-size: 0.75em;
   text-align: center;
   width: 90%;
   margin: 0 auto 25px;
   padding: 10px;
   line-height: 1;
   background-color: #eef;
   border: 2px solid #ccf;
   border-radius: 5px;
   display: block;
 }