.show-btn2 {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  background-color: white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.equal2 {
  grid-column-start: 4;
  grid-column-end: 6;
}

.btn2 {
  width: 60px;
  height: 60px;
  margin: 6px auto;
  font-size: 24px;
  font-weight: 500;
  cursor: pointer;
  border-radius: 50%;
  background: none;
  border: 0px;
}

.btn2:last-child {
  font-weight: medium;
  font-size: 40px;
  width: 110px;
  height: 50px;
  border-radius: 10px;
  color: white;
  background-color: #6dd5ed;
}

.btn2:hover {
  background-color: #6dd5ed;
  color: white;
  transition: background-color 0.3s ease;
}

.btn2:active {
  background-color: #48b1bf;
  color: white;
  transition: background-color 0.3s ease;
}

.clr2 {
  color: #d66d75;
}

.exp2 {
  color: #06beb6;
}
